@use "global" as *;

.p-form {
  border-top: 1px solid #eeeeee;
}

.p-form__content {
  display: flex;
  align-items: center;
  padding: rem(38) 0;
  border-bottom: 1px solid #eeeeee;
  @include mq(md) {
    flex-direction: column;
    align-items: flex-start;
    padding: rem(28) 0;
  }
}

.p-form__head {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: rem(313);
  @include mq(md) {
    max-width: 100%;
    padding-bottom: rem(20);
  }
}

.p-form__title {
  width: 100%;
  max-width: rem(263);
  display: block;
  font-size: rem(20);
  font-weight: $bold;
  cursor: pointer;
  padding-right: rem(40);
  @include mq(md) {
    width: fit-content;
    max-width: initial;
    padding-right: rem(20);
    font-size: rem(16);
  }
}

.p-form__add {
  display: block;
  color: $white;
  width: 100%;
  max-width: rem(50);
  text-align: center;
  line-height: 1.42;
  padding: rem(2);
  background-color: #e63333;
  @include mq(md) {
    font-size: rem(12);
    max-width: rem(40);
  }
}

.p-form__add.p-form__add--any {
  background-color: #707070;
}

.p-form__item {
  width: 100%;
  max-width: rem(800);
  margin-left: rem(38);
  // セレクトボックスのクリック領域増やす時はこちらに背景職
  // background-color: #e5e5e5;
  @include mq(md) {
    max-width: 100%;
    margin-left: 0;
  }
}

.p-form__text {
  width: 100%;
  padding: rem(18);
  font-size: rem(20);
  background-color: #e5e5e5;
  @include mq(md) {
    padding: rem(4) rem(10);
    font-size: rem(16);
  }
}

.p-form__content.p-form__content--textarea {
  align-items: flex-start;
}

.p-form__textarea {
  width: 100%;
  height: rem(193);
  font-size: rem(20);
  padding: rem(18);
  background-color: #e5e5e5;
  @include mq(md) {
    height: rem(144);
    padding: rem(4) rem(10);
    font-size: rem(16);
  }
}

