.p-form__btn {
  text-align: center;
  margin-top: rem(40);
  @include mq(md) {
    margin-top: rem(38);
  }
}

.p-form__btn .c-btn {
  transition: all 0.3s;
  &:hover {
    opacity: 0.8;
  }
  @include mq("md") {
    &:hover {
      opacity: 1;
    }
  }
}
